import axios from 'axios'

/*  */
let remainRequestNumber = 2

const initWeiXin = (currentURL) => {
  /*  GET  */
  axios
    .get(`authentication/signature?url=${currentURL}`)
    .then((respones) => {
      /*  SDK */
      window.jWeixin.config({
        debug     : false, // ,apialertpclogpc
        appId     : 'wxa53771df5d04e3d3', // 
        timestamp : respones.data.timestamp, // 
        nonceStr  : respones.data.noncestr, // 
        signature : respones.data.signature,// 
        jsApiList : ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ'] // JS
      })
    })

  /*  PUT  */
  window.jWeixin.error(function (error) {
    /*  */
    if (remainRequestNumber-- > 0) {
      axios
        .put(`authentication/signature?url=${currentURL}`)
        .then((respones) => {
          /*  SDK */
          window.jWeixin.config({
            debug     : false, // ,apialertpclogpc
            appId     : 'wxa53771df5d04e3d3', // 
            timestamp : respones.data.timestamp, // 
            nonceStr  : respones.data.noncestr, // 
            signature : respones.data.signature,// 
            jsApiList : ['onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ'] // JS
          })
        })
    }
  })
}

/*  */
const configShareTimeline = ({
  title,
  link,
  imgUrl,
}) => {
  window.jWeixin.onMenuShareTimeline({
    title,
    link,
    imgUrl,
    fail: function () {
      alert('')
    },
  })
}

/*  */
const configShareAppMessage = ({
  title,
  desc,
  link,
  imgUrl,
}) => {
  window.jWeixin.onMenuShareAppMessage({
    title,
    desc,
    link,
    imgUrl,
    fail: function () {
      alert('')
    },
  })
}

/*  QQ  */
const configShareQQ = ({
  title,
  desc,
  link,
  imgUrl,
}) => {
  window.jWeixin.onMenuShareQQ({
    title,
    desc,
    link,
    imgUrl,
    fail: function () {
      alert(' QQ ')
    },
  })
}

const configWeiXinShare = ({
  title,
  desc,
  link,
  imgUrl,
}) => {
  configShareTimeline({
    title,
    link,
    imgUrl,
  })
  configShareAppMessage({
    title,
    desc,
    link,
    imgUrl,
  })
  configShareQQ({
    title,
    desc,
    link,
    imgUrl,
  })
}

/* eslint-disable */
export {
  configShareTimeline,
  configShareAppMessage,
  configShareQQ,
  configWeiXinShare,
  initWeiXin,
}
/* eslint-disable */



